import React, { useState } from "react";
import styled from "styled-components";
import { checkUser } from "../../API/User/User";

const Input = styled.input`
  width: calc(100% - 11px);
  height: 50px;
  border-radius: 5px;
  border-style: solid;
  border-width: 1px;
  margin: 5px 0px;
  padding: 0px;
  padding-left: 10px;
  border-color: silver;
  @media screen and (max-width: 768px) {
    font-size: 12px;
  }
`;

const InputHeader = styled.div`
  width: calc(100% - 1px);
  margin-top: 10px;
`;

const InputButton = styled.button`
  width: 100%;
  height: 50px;
  margin-top: 10px;
  background-color: #83b551;
  border-style: none;
  color: white;
  border-radius: 5px;
  @media screen and (max-width: 768px) {
    font-size: 12px;
  }
`;

const CheckButton = styled.button`
  width: 100%;
  height: 50px;
  margin-top: 10px;
  background-color: white;
  border-style: solid;
  border-color: #83b551;
  border-width: 1px;
  color: #83b551;
  border-radius: 5px;
  @media screen and (max-width: 768px) {
    font-size: 12px;
  }
  cursor:pointer;
`;

const ErrMessage = styled.div`
font-size:13px;
`;

const UserId = ({ addUser, changeBtn }: any) => {
  const [userId, setUserId] = useState("");
  const [errMsg, setErrMsg] = useState("");

  const onChangeUserId = (e: any) => {
    setUserId(e.target.value);
  };

  const clickBtn = async () => {
    if (userId === "") {
      setErrMsg(" ");
    } else {
      const userInfo = {
        userId: userId,
      };
      const data: any = await checkUser(userInfo);
      if (data.json.result === true) {
        setErrMsg("  ");
      } else {
        const key="userId";
        const value=userId;
        addUser(key, value);
        changeBtn("UserPw");
      }
    }
  };

  const checkId = async () => {
    if (userId === "") {
      setErrMsg(" ");
    } else {
      const userInfo = {
        userId: userId,
      };
      const data: any = await checkUser(userInfo);
      if (data.json.result === true) {
        setErrMsg("  ");
      } else {
        setErrMsg("  ");
      }
    }
  };

  return (
    <>
      {errMsg !== "" ? <ErrMessage>{errMsg}</ErrMessage> : null}
      <InputHeader></InputHeader>
      <Input
        type="text"
        maxLength={20}
        placeholder=" "
        onChange={onChangeUserId}
        required
      ></Input>
      <CheckButton onClick={checkId}></CheckButton>
      <InputButton onClick={clickBtn}></InputButton>
    </>
  );
};

export default UserId;
