import React, { useState } from "react";
import styled from "styled-components";

const Input = styled.input`
  width: calc(100% - 11px);
  height: 50px;
  border-radius: 5px;
  border-style: solid;
  border-width: 1px;
  margin: 5px 0px;
  padding: 0px;
  padding-left: 10px;
  border-color: silver;
  @media screen and (max-width: 768px) {
    font-size: 12px;
  }
`;

const InputHeader = styled.div`
  width: calc(100% - 1px);
  margin-top: 10px;
`;

const InputButton = styled.button`
  width: 100%;
  height: 50px;
  margin-top: 10px;
  background-color: #83b551;
  border-style: none;
  color: white;
  border-radius: 5px;
  @media screen and (max-width: 768px) {
    font-size: 12px;
  }
  cursor:pointer;
`;

const ErrorMsgDiv = styled.div`
  width: 100%;
  text-align:center;
  font-size:13px;
`;

const UserPw = ({ addUser, changeBtn }: any) => {
  const [userPw, setUserPw] = useState("");
  const [userPw2, setUserPw2] = useState("");
  const [errMsg, setErrMsg] = useState("");

  const checkSamePW = () => {
    if(userPw !== userPw2){
        setErrMsg(' ');
        return false;
    } else {
        setErrMsg('');
        return true;
    }
  }

  const onChangeUserPw = (e: any) => {
    setUserPw(e.target.value);
  };

  const onChangeUserPw2 = (e: any) => {
    setUserPw2(e.target.value);
  };

  const clickBtn = () => {
    if(userPw === "" || userPw2 === ""){
        setErrMsg(' ');
    } else {
        const result = checkSamePW();
        if(result === true){
            const key = 'userPw';
            const value = userPw;
            addUser(key, value);
            changeBtn("UserName");
        }
    }
  };
0
  return (
    <>
      {errMsg !== "" ? <ErrorMsgDiv>{errMsg}</ErrorMsgDiv> : null}
      <InputHeader></InputHeader>
      <Input
        type="password"
        onChange={onChangeUserPw}
        placeholder=" "
        maxLength={20}
      />
      <Input
        type="password"
        onChange={onChangeUserPw2}
        placeholder="    "
        maxLength={20}
      />
      <InputButton onClick={clickBtn}></InputButton>
    </>
  );
};

export default UserPw;
